import axios from 'axios'
axios.defaults.baseURL = 'ec2-18-188-248-24.us-east-2.compute.amazonaws.com/api/'

export default {
    state: {
    },
    mutations: {
    },
    actions: {
        async sendFeedback({commit}, {email, text}) {
            commit('clearError')
            commit('setLoading', true)
            try {
                let response = await axios.post(
                    'feedback/',
                    {
                        "email": email,
                        "text": text
                    }
                );
                commit('setCode', response.data['code'])
                commit('setLoading', false)
            } catch (err) {
                commit('setLoading', false)
                commit('setError', err.message)
                console.log(err.response)
                throw err
            }
        }
    },
    getters: {
    }
}