<template>
<!--    class="xs4 sm4 md3"-->
<div>
    <v-navigation-drawer
            v-model="drawer"
            clipped
            right
            app
    >
        <v-list dense>
            <v-list-item>
                <v-combobox
                        v-model="chips"
                        :items="categories"
                        chips
                        label="Категории"
                        multiple
                        prepend-icon="filter_list"
                        solo
                >
                    <template v-slot:selection="{ attrs, item, select, selected }">
                        <v-chip
                                v-bind="attrs"
                                :input-value="selected"
                                close
                                @click="select"
                                @click:close="remove(item)"
                        >
                            <strong>{{ item }}</strong>&nbsp;

                        </v-chip>
                    </template>
                </v-combobox>
            </v-list-item>
            <v-list-item>
                <v-layout row wrap justify-center>
                    <p class="font-weight-bold text-center">Цена:</p>
                </v-layout>
            </v-list-item>
            <v-list-item>
                <v-row>
                    <v-col class="px-4">
                        <v-range-slider
                                v-model="range"
                                :max="minmax[1]"
                                :min="minmax[0]"
                                :thumb-size="30"
                                thumb-label="always"
                                class="align-center"
                                thumb-color="indigo"
                                track-color="grey"
                                color="indigo"
                                dark
                        >
                        </v-range-slider>
                    </v-col>
                </v-row>
            </v-list-item>
            <v-list-item>
                <v-layout row wrap justify-center>
                    <v-btn @click="filterProducts" class="ma-1">Применить</v-btn>
                    <v-btn @click="clearFilter" class="ma-1">Сбросить&nbsp;&nbsp;&nbsp;</v-btn>
                </v-layout>
            </v-list-item>
        </v-list>
    </v-navigation-drawer>
        <v-container>
            <v-layout row wrap justify-center>
                <v-flex
                        class="xs12 sm12 md4 pa-2"
                        v-for="(item,i) in products"
                        :key="i"
                >
                    <v-card>
                        <v-img
                                height="200px"
                                :src="item.image"
                        >

                        </v-img>
                        <v-card-title>{{item.name}}</v-card-title>
                        <v-card-text
                                class="text--primary"
                        >
                            <v-clamp autoresize :max-lines="3">{{item.description}}</v-clamp>
                        </v-card-text>
                        <v-card-actions>
                            <h3 class="red--text">{{item.discount_price ? item.discount_price : item.price }}</h3>
                            <h5>&nbsp;&nbsp;<del>{{ item.discount_price ? item.price : '' }}</del></h5>
                            <v-spacer></v-spacer>
                            <v-btn @click="openDetail(item)">Открыть</v-btn>
                            <v-btn color="#3fb5a3"
                                   @click="addToCart(item)"
                            >В корзину</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-flex>
            </v-layout>
            <v-dialog v-model="dialog" max-width="700px">
                <v-card>
                    <v-img
                            max-height="300px"
                            :src="detailView.image"
                    >

                    </v-img>
                    <v-card-title>{{detailView.name}}</v-card-title>
                    <v-card-text
                            class="text--primary"
                    >
                        {{detailView.description}}
                    </v-card-text>
                    <v-card-actions>
                        <h3 class="red--text">{{detailView.discount_price ? detailView.discount_price : detailView.price }}</h3>
                        <h5>&nbsp;&nbsp;<del>{{ detailView.discount_price ? detailView.price : '' }}</del></h5>
                        <v-spacer></v-spacer>
                        <v-btn color="#3fb5a3"
                               @click="addToCart(detailView)"
                        >В корзину</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-container>
</div>


</template>

<script>
import VClamp from 'vue-clamp'
export default {
    components: {
        VClamp
    },
    data () {
        return {
            detailView:{
                'name': '',
                'category': [],
                'image': '',
                'description': '',
                'price': 0,
                'discount_price': 0
            },
            dialog:false,
            range: [0, 10],
            chips: [],
            drawer: true,
            prod: null,
        }
    },
    computed: {
        items () {
            return this.$store.getters.getProducts
        },
        products () {
            return this.prod ? this.prod : this.$store.getters.getProducts
        },
        categories () {
            return this.$store.getters.getCategories
        },
        minmax () {
            return this.$store.getters.getPriceRange
        }

    },
    methods:{
        remove (item) {
            this.chips.splice(this.chips.indexOf(item), 1)
            this.chips = [...this.chips]
        },
        filterPrice (data, range) {
            return data.filter(item => item.price >= range[0] && item.price <= range[1])
        },
        filterCategory(data, cat) {
            return data.filter(item => cat.every(val => item.category.includes(val)))
        },
        filterProducts () {
            let f1 = this.chips.length > 0 ? this.filterCategory(this.products, this.chips) : this.products
            this.prod = this.filterPrice(f1, this.range)
        },
        clearFilter () {
            this.prod = null
            this.chips = []
            this.range = [0, this.minmax[1]]
        },
        addToCart(item) {
            this.$store.dispatch('setCartItem', {'item': item, 'fnc':'inc'})
        },
        openDetail(item) {
            this.detailView = Object.assign({}, item)
            this.dialog = true
        }
    }
}
</script>

<style>
    .v-slider__thumb-label {
        font-size: 10px !important
    }
</style>