import axios from 'axios'
axios.defaults.baseURL = 'ec2-18-188-248-24.us-east-2.compute.amazonaws.com/api/'

function getRandom(arr, n) {
    var result = new Array(n),
        len = arr.length,
        taken = new Array(len);
    if (n > len)
        throw new RangeError("getRandom: more elements taken than available");
    while (n--) {
        var x = Math.floor(Math.random() * len);
        result[n] = arr[x in taken ? taken[x] : x];
        taken[x] = --len in taken ? taken[len] : len;
    }
    return result;
}

export default {
    state: {
        categories: [],
        products: [],
        cart: [],
        code: "",
        status: ''
    },
    mutations: {
        setCategories(state, payload) {
            state.categories = Object.keys(payload).map(function(k) {
                return payload[k]['name']
            })
        },
        setProducts(state, payload) {
            state.products = payload
        },
        setCartItem(state, {item, fnc}) {
            let index = state.cart.indexOf(state.cart.filter(val => val.name === item.name)[0])
            if (index < 0) {
                state.cart.push({
                    'name': item.name,
                    'price': item.discount_price ? item.discount_price : item.price ,
                    'quantity': 1,
                    'basic_price': item.discount_price ? item.discount_price : item.price ,
                })
            } else {
                if (fnc === 'inc') {
                    state.cart[index]['quantity']++;
                    state.cart[index]['price'] = state.cart[index]['quantity'] * state.cart[index]['basic_price']
                } else if (fnc === 'del') {
                    state.cart.splice(index,1)
                } else {
                    if (state.cart[index]['quantity'] === 1) {
                        state.cart.splice(index,1)
                    } else {
                        state.cart[index]['quantity']--;
                        state.cart[index]['price'] = state.cart[index]['quantity'] * state.cart[index]['basic_price']
                    }
                }
            }
        },
        clearCart(state) {
            state.cart = []
        },
        setCode(state, payload) {
            state.code = payload
        },
        setStatus(state, payload) {
            state.status = payload
        }
    },
    actions: {
        async loadCategories ({ commit }) {
            commit('clearError')
            commit('setLoading', true)
            try {
                let response = await axios.get(
                    'categories/'
                );
                commit('setCategories', response.data)
                commit('setLoading', false)
            } catch (err) {
                commit('setLoading', false)
                commit('setError', err.message)
                console.log(err.response)
                throw err
            }
        },
        async loadProducts({commit}) {
            commit('clearError')
            commit('setLoading', true)
            try {
                let response = await axios.get(
                    'products/'
                );
                commit('setProducts', response.data)
                commit('setLoading', false)
            } catch (err) {
                commit('setLoading', false)
                commit('setError', err.message)
                console.log(err.response)
                throw err
            }
        },
        setCartItem({commit}, payload) {
            commit('setCartItem', payload)
        },
        clearCart({commit}) {
            commit('clearCart')
        },
        async postCart({commit}, {products, address}) {
            products = products.map(val => ({'quantity':val.quantity,'product': {'name':val.name }}))
            commit('clearError')
            commit('setLoading', true)
            try {
                let response = await axios.post(
                    'status/',
                    {
                        "cart":{
                            "products": products
                        },
                        "address": address
                    }
                );
                commit('setCode', response.data['code'])
                commit('setLoading', false)
            } catch (err) {
                commit('setLoading', false)
                commit('setError', err.message)
                console.log(err.response)
                throw err
            }
        },
        async loadStatus ({commit}, payload) {
            commit('clearError')
            commit('setLoading', true)
            try {
                let response = await axios.get(
                    'status/' + payload
                );
                commit('setStatus', response.data['status'])
                commit('setLoading', false)
            } catch (err) {
                commit('setLoading', false)
                commit('setError', err.message)
                console.log(err.response)
                throw err
            }
        }
    },
    getters: {
        getCategories(state) {
            return state.categories
        },
        getProducts(state) {
            return state.products
        },
        getProductsForCarousel(state) {
            return state.products.length>0 ? getRandom(state.products, 3) : state.products
        },
        getHomeProducts(state){
            return state.products.length>6 ? state.products.slice(0,6) : state.products
        },
        getPriceRange(state){
            if (state.products.length>0){
                let v1 = Array.from(state.products.map(item => item.discount_price ? item.discount_price : item.price));
                let max = v1.reduce((a,b)=> Math.max(a,b))
                return [0, max]
            } else {
                return [0, 1]
            }
        },
        getCart(state){
            return state.cart
        },
        getCartCount(state){
            return state.cart.length
        },
        getTotalPrice(state){
            return state.cart.reduce((a, b) => parseInt(a,10)+ parseInt(b.price,10), 0)
        },
        getCode(state) {
            return state.code
        },
        getStatus(state) {
            return state.status
        }
    }
}