<template>
    <v-content>
        <v-container
                class="fill-height"
                fluid
        >
            <v-row
                    align="center"
                    justify="center"
            >
                <v-col
                        cols="12"
                        sm="8"
                        md="4"
                >
                    <v-card class="elevation-12">
                        <v-toolbar
                                color="indigo"
                                dark
                                flat
                        >
                            <v-toolbar-title>Форма обратной связи</v-toolbar-title>
                        </v-toolbar>
                        <v-card-text>
                            <v-form ref="form"
                                    v-model="valid"
                            >
                                <v-text-field
                                        label="E-mail адрес для связи"
                                        name="login"
                                        prepend-icon="mail"
                                        type="text"
                                        v-model="email"
                                        :rules="emailRules"
                                />
                                <v-textarea
                                        id="comment"
                                        label="Ваше сообщение"
                                        outlined
                                        prepend-icon="comment"
                                        rows="4"
                                        v-model="text"
                                        :rules="rules"
                                />
                            </v-form>
                        </v-card-text>
                        <v-card-actions>
                            <div class="flex-grow-1"></div>
                            <v-btn color="indigo"
                                   class="mb-1 white--text"
                                   :disabled="!valid"
                                   @click="send"
                            >
                                Отправить
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
        </v-container>
    </v-content>
</template>

<script>
export default {
    data () {
        return {
            email: '',
            text: '',
            valid: true,
            emailRules: [
                v => !!v || 'Данное поле не должно быть пустым',
                v => /.+@.+\..+/.test(v) || 'E-mail указан не верно',
            ],
            rules: [
                v => !!v || 'Данное поле не должно быть пустым',
            ]
        }
    },
    methods: {
        send () {
            this.$store.dispatch('sendFeedback', {'email':this.email, 'text':this.text})
            this.$refs.form.reset()
        }
    }
}
</script>

