<template>
    <v-container>
        <v-form ref="form"
                v-model="valid"
        >
            <v-row justify="center">
                <v-col cols="12" sm="6" md="6">
                    <v-text-field
                            v-model="code"
                            label="Код заказа"
                            outlined
                            counter="15"
                            :rules="rules"
                            append-icon="mdi-magnify"
                            @click:append="search"
                    />
                </v-col>
            </v-row>
        </v-form>
        <h2 v-if="status" class="text-center">Ваш заказ {{ status }}</h2>
    </v-container>
</template>

<script>
export default {
    data () {
        return {
            rules: [
                value => !!value || 'Укажите код заказа.',
                value => (value || '').length === 15 || 'Код должен содержать 15 символов'
            ],
            valid: true,
            code: ''
        }
    },
    methods: {
        search () {
            if (this.$refs.form.validate()) {
                this.$store.dispatch('loadStatus', this.code)
            }
        }
    },
    computed: {
        status () {
            return this.$store.getters.getStatus
        }
    }
}
</script>

