<template>
    <v-container>
        <v-data-table
                :headers="headers"
                :items="items"
                sort-by="calories"
                class="elevation-1 mt-2"
                hide-default-footer
        >
            <template v-slot:top>
                <v-toolbar flat color="white">
                    <v-toolbar-title>Корзина</v-toolbar-title>
                </v-toolbar>
            </template>
            <template v-slot:item.action="{ item }">
                <v-icon
                        small
                        class="mr-2"
                        @click="plusItem(item)"
                >
                    mdi-plus
                </v-icon>
                <v-icon
                        small
                        class="mr-2"
                        @click="minusItem(item)"
                >
                    mdi-minus
                </v-icon>
                <v-icon
                        small
                        @click="deleteItem(item)"
                >
                    delete
                </v-icon>
            </template>
        </v-data-table>
        <div class="text-center pt-2">
            <v-dialog v-model="dialog" persistent max-width="600px">
                <template v-slot:activator="{ on }">
                    <v-btn color="primary"
                           dark
                           v-on="on"
                           :disabled="items.length === 0"
                    >Оформить заказ</v-btn>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="headline">Оформление заказа</span>
                    </v-card-title>
                    <v-card-text>
                        <v-container>
                            <v-form
                                    ref="form"
                                    v-model="valid"
                            >
                                <v-row>
                                    <v-col cols="12">
                                        <v-text-field v-model='address' label="Адресс" :rules="rules"/>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <h2>Цена заказа:</h2>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <h3 class="text-right">{{totalPrice}}</h3>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-expansion-panels>
                                            <v-expansion-panel>
                                                <v-expansion-panel-header>Товары в корзине</v-expansion-panel-header>
                                                <v-expansion-panel-content>
                                                    <v-list>
                                                        <v-list-item
                                                                v-for="(item, i) in items"
                                                                :key="i"
                                                        >
                                                            <v-list-item-content>
                                                                <v-list-item-title v-text="item.name"/>
                                                            </v-list-item-content>
                                                        </v-list-item>
                                                    </v-list>
                                                </v-expansion-panel-content>
                                            </v-expansion-panel>
                                        </v-expansion-panels>
                                    </v-col>
                                </v-row>
                            </v-form>
                        </v-container>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer/>
                        <v-btn color="blue darken-1" text @click="dialog = false">Закрыть</v-btn>
                        <v-btn color="blue darken-1"
                               text
                               @click="checkout"
                               :disabled="!valid"
                        >Оформить</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
            <v-dialog
                    v-model="dialog2"
                    persistent
                    width="500"
            >
                <v-card v-if="answer && loading">
                    <v-card-text>
                        Происходит оформление заказа
                        <v-progress-linear
                                :active="loading"
                                indeterminate
                                color="primary"
                                class="mb-0"
                        />
                    </v-card-text>
                </v-card>
                <v-card v-else>
                    <v-card-title>
                        <span class="headline">Заказ оформлен</span>
                    </v-card-title>
                    <v-card-text>
                        <v-container>
                            <p class="text-center">С помощью данного кода вы можете отслеживать состояние вашего заказа*</p>
                            <h2 class="text-center green--text" >{{ code }}</h2>
                        </v-container>
                        <small class="text-center">*Пожалуйста, запишите его и не передавайте третьим лицам</small>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer/>
                        <v-btn color="blue darken-1" text @click="closeWindow">Закрыть</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </div>
    </v-container>
</template>

<script>
export default {
    data () {
        return {
            rules: [
                value => !!value || 'Укажите адрес доставки.'
            ],
            valid: true,
            address: '',
            dialog: false,
            dialog2: false,
            answer: false,
            // loading: false,
            headers: [
                {
                    text: "Название",
                    value: 'name'
                },
                {
                    text: "Цена",
                    value: 'price'
                },
                {
                    text: "Количество",
                    value: 'quantity'
                },
                {
                    text: 'Действия',
                    value: 'action',
                    sortable: false
                },
            ],
        }
    },
    computed: {
        items () {
            return this.$store.getters.getCart
        },
        totalPrice () {
            return this.$store.getters.getTotalPrice
        },
        loading () {
            return this.$store.getters.loading
        },
        code () {
            return this.$store.getters.getCode
        }
    },
    methods: {
        deleteItem (item) {
            this.$store.dispatch('setCartItem', {'item': item, 'fnc':'del'})
        },
        plusItem (item) {
            this.$store.dispatch('setCartItem', {'item': item, 'fnc':'inc'})
        },
        minusItem (item) {
            this.$store.dispatch('setCartItem', {'item': item, 'fnc':'dec'})
        },
        checkout () {
            this.answer = confirm('Вы действительно хотите оформить заказ?')
            if (this.answer === true) {
                this.dialog2 = true;
                this.$store.dispatch('postCart', {
                    'products': this.items,
                    'address': this.address
                })
            } else {
                this.dialog = false
            }
        },
        closeWindow () {
            this.answer = false;
            this.dialog = false;
            this.dialog2 = false;
            this.$store.dispatch('clearCart')
        }
    }
}
</script>

