<template>
<v-container>
  <v-carousel
          hide-delimiters
          class="mt-2"
  >
    <v-carousel-item
            v-for="(item,i) in carousel"
            :key="i"
            :src="item.image"
    />
  </v-carousel>
  <h1
          class="text-center mt-3"
  >Рекомендуемые товары</h1>
  <v-layout row wrap justify-center>
    <v-flex
            class="xs12 sm12 md4 pa-2"
            v-for="(item,i) in products"
            :key="i"
    >
      <v-card>
        <v-img
                height="200px"
                :src="item.image"
        >

        </v-img>
        <v-card-title>{{item.name}}</v-card-title>
        <v-card-text
              class="text--primary"
      >
          <v-clamp autoresize :max-lines="3">{{item.description}}</v-clamp>
      </v-card-text>
        <v-card-actions>
          <h3 class="red--text">{{item.discount_price ? item.discount_price : item.price }}</h3>
          <h5>&nbsp;&nbsp;<del>{{ item.discount_price ? item.price : '' }}</del></h5>
          <v-spacer/>
          <v-btn @click="openDetail(item)">Открыть</v-btn>
          <v-btn color="#3fb5a3"
                 @click="addToCart(item)"
          >В корзину</v-btn>
        </v-card-actions>
      </v-card>
      <v-dialog v-model="dialog" max-width="700px">
        <v-card>
          <v-img
                  max-height="300px"
                  :src="detailView.image"
          >

          </v-img>
          <v-card-title>{{detailView.name}}</v-card-title>
          <v-card-text
                  class="text--primary"
          >
            {{detailView.description}}
          </v-card-text>
          <v-card-actions>
            <h3 class="red--text">{{detailView.discount_price ? detailView.discount_price : detailView.price }}</h3>
            <h5>&nbsp;&nbsp;<del>{{ detailView.discount_price ? detailView.price : '' }}</del></h5>
            <v-spacer></v-spacer>
            <v-btn color="#3fb5a3"
                   @click="addToCart(detailView)"
            >В корзину</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
import VClamp from 'vue-clamp'
export default {
  components: {
    VClamp
  },
  data() {
    return {
      detailView:{
        'name': '',
        'category': [],
        'image': '',
        'description': '',
        'price': 0,
        'discount_price': 0
      },
      dialog:false
    }
  },
  methods: {
    addToCart(item) {
      this.$store.dispatch('setCartItem', {'item': item, 'fnc':'inc'})
    },
    openDetail(item) {
      this.detailView = Object.assign({}, item)
      this.dialog = true
    }
  },
  computed: {
    products () {
      return this.$store.getters.getHomeProducts
    },
    carousel () {
      return this.$store.getters.getProductsForCarousel
    }
  }
}
</script>
